import './Game.css';
import React from 'react';
import HaiAngWork from './HaiAngWork';
import { orderedWorks } from './GameConstants';

class Game extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            workIndex: 0,
            message: "",
            win: []
        };
    }

    onClickNext() {
        const index = this.state.workIndex;
        if (index < orderedWorks.length - 1)
            this.setState({ workIndex: index + 1, message: "" });
    }

    onClickPrevious() {
        const index = this.state.workIndex;
        if (index > 0)
            this.setState({ workIndex: index - 1, message: "" });
    }

    getWork(index) {
        return orderedWorks[index];
    }

    onTalk = (who, content) => {
        this.setState({
            message:
                (<span>
                    <span>{who}</span>：<span>{content}</span>
                </span>
                )
        });
    }

    onWin = (id) => {
        this.setState({ win: [...this.state.win, id] });
    }

    getMessage = () => {
        if (this.state.message)
            return (<h3>{this.state.message}</h3>)
    }

    isWin = (id) => {
        return (this.state.win.includes(id))
    }

    getReward = () => {
        if (this.state.win.length === 5) {
            return <h1>恭喜全破</h1>
        }
    }

    render() {
        const work = this.getWork(this.state.workIndex);
        return (
            <div id="game">
                {this.getReward()}
                <div id="gallery">
                    <button className="gallery_button" onClick={() => this.onClickPrevious()}>←</button>
                    {HaiAngWork.build(work.id, this.isWin(work.id), this.onTalk, this.onWin)}
                    <button className="gallery_button" onClick={() => this.onClickNext()}>→</button>
                </div>
                <div>
                    {this.getMessage()}
                </div>
            </div>
        )
    }
}

export default Game;