import React from 'react';
import { worksById, conversationScript } from './GameConstants';
import Spritesheet from 'react-responsive-spritesheet';



import Zoom from 'react-medium-image-zoom';
import 'react-medium-image-zoom/dist/styles.css';

import { PlayButton, Timer } from 'react-soundplayer/components';
import { withCustomAudio } from 'react-soundplayer/addons';

import whealeSound from '../sound/52hz.mp3';
import bearsPhotos from '../img/sprite/coffee_photos.png';
import mail from '../img/sprite/mail.png';
import unopenMail from '../img/sprite/mail_unopen.png';
import redTape from '../img/sprite/tape_red.png';
import greenTape from '../img/sprite/tape_green.png';
import purpleTape from '../img/sprite/tape_purple.png';
import whiteTape from '../img/sprite/tape_white.png';

class HaiAngWork extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            conversationIndex: -1,
            isEnd: this.props.isWin
        };
    }

    static build(id, isWin, onTalk, onWin) {
        const work = worksById[id]

        if (isWin)
            return <WinWork />

        switch (id) {
            case worksById.CATBOAT.id:
                return <BoatWork key={id} data={work} isWin={isWin} onTalk={onTalk} onWin={onWin} />
            case worksById.CANBEER_FROG.id:
                return <CanbeerFrog key={id} data={work} isWin={isWin} onTalk={onTalk} onWin={onWin} />
            case worksById.BEARS_COFFEE.id:
                return <BearsCoffee key={id} data={work} isWin={isWin} onTalk={onTalk} onWin={onWin} />
            case worksById.WALKING_CAT.id:
                return <WalkingCat key={id} data={work} isWin={isWin} onTalk={onTalk} onWin={onWin} />
            case worksById.WINDOW_AND_CAT.id:
                return <WindowAndCat key={id} data={work} isWin={isWin} onTalk={onTalk} onWin={onWin} />
            default:
                return <HaiAngWork key={id} data={work} isWin={isWin} onTalk={onTalk} onWin={onWin} />
        }
    }

    getWork = () => {
        return this.props.data;
    }

    getMechanism = () => {
    }

    talkNext = () => {
        const workId = this.getWork().id;
        let index = this.state.conversationIndex + 1;
        this.setState({ conversationIndex: this.state.conversationIndex + 1 });
        const conversation = conversationScript[workId][index];
        if (conversation)
            this.props.onTalk(conversation.who, conversation.content)
    }

    onDivMouseDown = (e) => {
        this.talkNext()
    }

    render() {
        console.log("rerender:", this.props.data);
        const work = this.getWork();

        return (
            <div className="haiang_workplace">
                <div className="frame"
                    onMouseDown={this.onDivMouseDown}>
                    <Spritesheet
                        className="haiang_work"
                        style={{ width: "450px" }}
                        image={work.src}
                        startAt={work.start + 1}
                        endAt={work.start + work.steps}
                        direction={`forward`}
                        widthFrame={125}
                        heightFrame={125}
                        steps={work.steps}
                        fps={work.fps}
                        timeout={0}
                        loop
                        isResponsive
                        getInstance={spritesheet => {
                            this.spritesheeInstance = spritesheet;
                        }}
                    />
                </div>
                <div className="introduction_box">
                    {work.name}
                </div>
                <br />
                <div className="work_mechanism">
                    {this.getMechanism()}
                </div>

            </div>
        )
    }
}

class WinWork extends HaiAngWork {
    render() {
        return (
            <div className="haiang_workplace">
                <div className="frame">
                    贏了
                </div>
            </div>
        )
    }
}

class BoatWork extends HaiAngWork {

    onDivMouseDown = (e) => {
        if (this.isGotFish(e)) {
            this.props.onTalk("你", "(抓到一隻魚了)");
            this.props.onWin(this.getWork().id);
        } else
            this.talkNext();
    }

    isGotFish = (e) => {
        return this.isOnFishman(e) && this.isFishAroundRod()
    }

    isFishAroundRod = () => {
        const frame = this.spritesheeInstance.getInfo("frame");
        return this.isBetween(frame, 143, 146) || this.isBetween(frame, 167, 169);
    }

    isOnFishman = (e) => {
        let target = e.currentTarget.getBoundingClientRect();
        const x = (e.clientX - target.left) / target.width * 100;
        const y = (e.clientY - target.top) / target.height * 100;
        return this.isBetween(x, 0, 45) && this.isBetween(y, 19, 80);
    }

    isBetween = (target, min, max) => {
        return (target >= min && target <= max);
    }
}


class CanbeerFrog extends HaiAngWork {

    constructor(props) {
        super(props);
        this.state = { ...this.state, colors: [] };
    }

    getMechanism = () => {
        return (
            <div>
                <input type="password" defaultValue={this.getAnswer()} readOnly />
                <button onClick={this.onEnter}>Enter</button>
                <ul>
                    {["R", "G", "W", "P"]
                        .map((v) => { return this.getButton(v) })}
                </ul>
            </div>
        )
    }


    getButton = (answer) => {
        return (
            <li>
                <button onClick={() => this.inputAnswer(answer)}>
                    <img src={{ "R": redTape, "G": greenTape, "W": whiteTape, "P": purpleTape }[answer]} />
                </button>
            </li>
        )
    }

    getAnswer = () => {
        let answer = "";
        for (let i in this.state.colors)
            answer += ` ${this.state.colors[i]}`;
        return answer;
    }

    inputAnswer = (color) => {
        if (this.state.colors.length < 5) {
            const newColors = [...this.state.colors, color];
            this.setState({ colors: newColors });
        }
    }

    onEnter = () => {
        if (this.isRightAnswer(this.state.colors)) {
            this.props.onWin(this.getWork().id);
            this.props.onTalk("鎖", "(打開了)");
        } else {
            this.props.onTalk("鎖", "(喀答一聲，毫無反應)");
            this.setState({ colors: [] });
        }
    }

    isRightAnswer = (colors) => {
        return ["G","R","G","R","P"]
            .every((v, i) => v === colors[i]);
    }
}


class BearsCoffee extends HaiAngWork {
    constructor(props) {
        super(props);
        this.state = { ...this.state, hours: 0, minutes: 0 };
    }

    getMechanism = () => {
        return (
            <div>
                <div>

                    <Zoom>
                        <img id="bears_photos" src={bearsPhotos} />
                    </Zoom>
                </div>
                <input defaultValue={6} type="number" min={0} max={12}
                    onChange={(e) => { this.setState({ hours: e.target.value }) }} />
                ：
                <input defaultValue={15} type="number" min={0} max={45} step={15}
                    onChange={(e) => { this.setState({ minutes: e.target.value }) }} />
                <button onClick={this.inputAnswer}>ENTER</button>
            </div>
        )
    }

    inputAnswer = () => {
        if (this.state.hours === "10" && this.state.minutes === "30") {
            this.props.onWin(this.getWork().id);
            this.props.onTalk("時鐘", "(打開了)");
        } else
            this.props.onTalk("時鐘", "(喀答一聲，毫無反應)");
    }
}


class WalkingCat extends HaiAngWork {
    constructor(props) {
        super(props);
        this.state = { ...this.state, code: "", isLetterOpen: false };
    }

    getMechanism = () => {
        return (
            <div>
                <div onClick={(e) => {
                    if (!this.state.isLetterOpen) {
                        this.props.onTalk("你", "(打開了一封有密碼的信)");
                        this.setState({ isLetterOpen: true })
                    }
                }}>
                    {this.getLetter()}
                </div>
                <input onChange={(e) => { this.setState({ code: e.target.value }) }} />
                <button onClick={this.inputAnswer}>ENTER</button>
            </div>
        )
    }

    getLetter = () => {
        if (!this.state.isLetterOpen) {
            return (
                <img id="walking_cats_mail_unopen" src={unopenMail} />
            )
        } else {
            return (
                <Zoom>
                    <img id="walking_cats_mail" src={mail} />
                </Zoom>
            )
        }
    }

    inputAnswer = () => {
        console.log(this.state.code.toLowerCase)
        if (this.state.code.toLowerCase() === "sea") {
            this.props.onWin(this.getWork().id);
            this.props.onTalk("機關", "(打開了)");
        } else
            this.props.onTalk("機關", "(喀答一聲，毫無反應)");
    }
}


class WindowAndCat extends HaiAngWork {
    constructor(props) {
        super(props);
        this.state = { ...this.state, hertz: 250 };
    }

    getMechanism = () => {
        return (
            <div>
                <div>
                    <SoundPlayer
                        streamUrl={whealeSound}
                        preloadType="auto" />
                </div>
                <h1>{this.state.hertz}Hz</h1>
                <input type="range" defaultValue={250} min="36" max="276" step="4" onChange={(e) => { this.setState({ hertz: e.target.value }) }} />
                <button onClick={this.inputAnswer}>ENTER</button>
            </div>
        )
    }

    inputAnswer = () => {
        if (this.state.hertz === "52") {
            this.props.onWin(this.getWork().id);
            this.props.onTalk("機關", "(打開了)");
        } else
            this.props.onTalk("機關", "(喀答一聲，毫無反應)");

    }
}


const SoundPlayer = withCustomAudio(props => {
    return (
        <div>
            <PlayButton {...props} />
            <Timer {...props} />
        </div>
    );
});

export default HaiAngWork;